import React from "react";
import Uploady from "@rpldy/uploady";
import UploadPreview from "@rpldy/upload-preview";
import UploadButton from "@rpldy/upload-button";
import { useItemFinishListener ,useItemProgressListener,useBatchStartListener,useBatchFinishListener} from "@rpldy/uploady";
import { useState } from "react";

const UploadImageResponse = ({ setImg ,setImgPredict,loading,setLoading}) => {
  
  useItemProgressListener(()=>{
    setLoading(true)
  })
  useItemFinishListener((item) => {
    let tmp = "data:image/png;base64," + item.uploadResponse.data.encoded_img
    let tmpRmBg = "data:image/png;base64," + item.uploadResponse.data.predict
    const data = new Response(JSON.stringify(item));
    if ('caches' in window) {
      // Opening given cache and putting our data into it
      caches.open("data").then((cache) => {
        cache.put("src", data);
      });
    }
    setImg(tmp);
    setImgPredict(tmpRmBg);
    
    // localStorage.setItem("srcImg",tmp)
    // localStorage.setItem("srcRmBg",tmpRmBg)
    setLoading(false)
    
    
  });
  
  return <UploadButton extraProps={{disabled:loading}}><i  style={{color:"blue"}} className="fa fa-upload" aria-hidden="true"></i>Upload images</UploadButton>;
};

export default function LoadImage({setImg,setImgPredict,loading,setLoading}) {
  
  return (
    <Uploady
      method="POST"
      destination={{
        url: "http://127.0.0.1:8000/img",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "X-Requested-With",
          "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS"
        },
      }}
    >
      <UploadImageResponse setImg={setImg} setImgPredict={setImgPredict} loading={loading} setLoading={setLoading}/>
    </Uploady>
  );
}
