import React from "react";
import Layout from "../components/Layout";
import "./Login.css";
import facebook from "../assets/images/facebook.svg";
import google from "../assets/images/google.svg";
import {useState} from 'react'
import {useHistory} from 'react-router-dom'
import { useUserAuth } from "../context/UserAuthContext";
export default function Login() {
  const dataForm = [{id:"0111",title:"Login",type:"login"},{id:"0112",title:"Sign up",type:"signUp"}]
  const [form,getForm] = useState("login")
  const handlerClick =(item)=>{
    getForm(item.type)
  }
  return (
    <Layout>
      <div className="login-container">
        <div className="login-text">
          <h2 style={{ margin: "auto", textAlign: "center", fontSize: "30px" }}>
            Login with your Kaleido Account
          </h2>
          <p>or create a new account for free</p>
        </div>
        <div className="login-form">
          <div>
            <ul>
              {dataForm.map(item=>{
                return <li key={item.id} onClick={()=>handlerClick(item)} className={item.type===form ? "active-login" :""}>{item.title}</li>
              })}
            </ul>
          </div>
          <div className="login-btn">
            <button>
              <img
                style={{
                  width: "20px",
                  height: "20px",
                  marginRight: "4px",
                  marginLeft: "30px",
                }}
                src={facebook}
                alt="#"
              />
              Continue with Facebook
            </button>
            <button>
              <img
                style={{
                  width: "20px",
                  height: "20px",
                  marginRight: "4px",
                  marginLeft: "30px",
                }}
                src={google}
                alt="#"
              />
              Continue with Google
            </button>
          </div>
          <div
            className="text-break"
            style={{
              textAlign: "center",
              position: "relative",
              overflow: "hidden",
              width: "80%"
            }}
          >
            or
          </div>
          {form === "login" ? <LoginForm/> : <SignUpForm/>}
        </div>
        
      </div>
    </Layout>
  );
}

const LoginForm = () => {
  const [email,setEmail] = useState("")
  const [password,setPassword] = useState("")
  const history = useHistory()
  const { logIn } = useUserAuth();
  // email==="thorpham@gmail.com" && password==="123456"
  const handleLoginForm = async (e)=>{
    e.preventDefault()
    try{
      await logIn(email, password);
      history.push("/")
      setEmail("")
      setPassword("")
    }catch(e){
      alert(e.message)
      console.log(e.message)
    }
    
  }
  return (
    <div className="login-email">
      <input type="email" placeholder="Email" onChange={(e)=>setEmail(e.target.value)}></input>

      <input type="password" placeholder="Password" onChange={(e)=>setPassword(e.target.value)}></input>

      <label class="container-checkbox">
        <input
          type="checkbox"
          checked="checked"
          style={{ width: "15px", marginRight: "3px" }}
        />
        Remeber me
      </label>
      <button
        style={{
          width: "120px",
          height: "40px",
          color: "#fff",
          cursor: "pointer",
          backgroundColor: "rgb(38, 153, 251)",
          border: "none",
          fontWeight: "bold",
          borderRadius: "3px",
          letterSpacing: "1px",
        }}

        onClick={handleLoginForm}
      >
        Login
      </button>
    </div>
  );
};


const SignUpForm = ()=>{
  const [email, setEmail] = useState("");
  const [error, setError] = useState("");
  const [password, setPassword] = useState("");
  const { signUp } = useUserAuth();
  const history = useHistory()
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    try {
      await signUp(email, password);
      history.push("/")
    } catch (err) {
      setError(err.message);
    }
  };
  return  <div className="signup-form">
  <div>
    <label >Email</label>
    <input type="email" placeholder="Email" onChange={(e)=>setEmail(e.target.value)}></input>
  </div>
  <div>
    <label>Password (6 characters minimum)</label>
    <input type="password" placeholder="Password" onChange={(e)=>setPassword(e.target.value)}></input>
  </div>
  <div>
    <label>Password confirmation</label>
    <input type="password" placeholder="Password"></input>
  </div>
  <div className="sign-bottom">
  <input
      type="checkbox"
      checked="checked"
      style={{ width: "15px", marginRight: "3px" }}
    />
  <label >  I agree to the Terms </label>
    
  
  </div>
  <button
    style={{
      width: "120px",
      height: "40px",
      color: "#fff",
      cursor: "pointer",
      backgroundColor: "rgb(38, 153, 251)",
      border: "none",
      fontWeight: "bold",
      borderRadius: "3px",
      letterSpacing: "1px",
      marginBottom:"1rem"
    }}
    onClick={handleSubmit}
  >
    Register
  </button>
</div>
}