const Option = ({ unit, total, price, type }) => {
  return (
    <tr>
      <td>
        <div style={{ marginRight: "50px" }}>
          <i
            style={{ marginRight: "5px", color: "rgb(38, 153, 251)" }}
            class="active-status-icon fas fa-dot-circle"
          ></i>
          {unit} credits
          <small>/ month</small>
        </div>
      </td>
      <td>
        <div>
          <span>$ {total}</span>
        </div>
      </td>
      <td style={{ marginRight: "auto" }}>
        <div>
          <strong>
            <span>
              <span style={{ fontWeight: "bold" }}>$ {price}</span>/ image
            </span>
          </strong>
        </div>
      </td>
    </tr>
  );
};

const Btn = ({ btn, text ,handleCheckOut}) => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        minHeight: "120px",
        justifyContent: "center",
        alignItems: "center",
        flex: "1",
      }}
    >
      <button
        onClick={handleCheckOut}
        style={{
          color: "#fff",
          backgroundColor: "rgb(38, 153, 251)",
          border: "none",
          height: "40px",
          width: "150px",
          borderRadius: "3px",
          padding: "2px",
          cursor: "pointer",
        }}
      >
        {btn}
      </button>
      <p
        style={{
          color: "rgb(69, 69, 69)",
          fontSize: "12px",
          marginTop: "3px",
          width: "80%",
        }}
      >
        {text}
      </p>
    </div>
  );
};

const ButtonShowCase = ({ question, answers, idx }) => {
  const [show, setShow] = useState(false);
  const handleClickShow = (idx) => {
    setShow(!show);
  };
  return (
    <div
      style={{ padding: "10px", border: "1px solid #ddd" }}
      className="btn-show-case-container"
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <p
          style={{ textAlign: "left", fontSize: "20px", color: "#5f5f5f" }}
          className="btn-question"
        >
          {question}
        </p>
        {!show ? (
          <i
            onClick={() =>
              handleClickShow(
                dataQuestionAnswer.filter((item) => item.id === idx)
              )
            }
            className="fa-solid fa-plus"
            style={{
              color: "rgb(38, 153, 251)",
              cursor: "pointer",
              fontSize: "20px",
              fontWeight: "bold",
            }}
          ></i>
        ) : (
          <i
            onClick={() =>
              handleClickShow(
                dataQuestionAnswer.filter((item) => item.id === idx)
              )
            }
            className="fa-solid fa-minus"
            style={{
              color: "rgb(38, 153, 251)",
              cursor: "pointer",
              fontSize: "20px",
              fontWeight: "bold",
            }}
          ></i>
        )}
      </div>
      {show && (
        <>
          {answers.map((item, idx) => {
            return (
              <>
                <br></br>
                <p
                  key={idx}
                  style={{
                    textAlign: "left",
                    fontSize: "16px",
                    color: "#5f5f5f",
                  }}
                  className="btn-answer"
                >
                  {item}
                </p>
              </>
            );
          })}
        </>
      )}
    </div>
  );
};
